import * as THREE from "three";
import { isCurrencySymbol, isPacmanPart } from "../animation/util";

export function setObjectOpacity(
  object: THREE.Object3D,
  opacity: number,
  options?: {
    preserveTransmission?: boolean;
    skipCurrencySymbols?: boolean;
  }
): void {
  const preserveTransmission = options?.preserveTransmission !== false;

  forEachMaterial(
    object,
    (mat: any) => {
      setMaterialOpacity(mat, opacity, preserveTransmission);
    },
    {
      skipCurrencySymbols: options?.skipCurrencySymbols === true,
    }
  );
}

export function setMaterialOpacity(
  material: THREE.Material,
  opacity: number,
  preserveTransmission: boolean = true
): void {
  const mat = material as any;
  const currentOpacity = mat.opacity ?? 1;
  const currentTransparent = mat.transparent ?? false;

  const hasTransmission =
    mat.transmission !== undefined && mat.transmission > 0;

  let shouldBeTransparent: boolean;
  if (preserveTransmission && hasTransmission) {
    shouldBeTransparent = true;
  } else if (opacity > 0) {
    shouldBeTransparent = true;
  } else {
    shouldBeTransparent = false;
  }

  const opacityChanged = Math.abs(currentOpacity - opacity) >= 0.0001;
  const transparentChanged = currentTransparent !== shouldBeTransparent;

  if (!opacityChanged && !transparentChanged) {
    return;
  }

  if (opacityChanged) {
    mat.opacity = opacity;
  }

  if (transparentChanged) {
    mat.transparent = shouldBeTransparent;
  }

  if ((opacityChanged || transparentChanged) && mat.needsUpdate !== undefined) {
    mat.needsUpdate = true;
  }
}

export function setMaterialTransparent(
  material: THREE.Material,
  transparent: boolean,
  preserveTransmission: boolean = true
): void {
  const mat = material as any;
  const currentTransparent = mat.transparent ?? false;

  const shouldBeTransparent =
    preserveTransmission &&
    mat.transmission !== undefined &&
    mat.transmission > 0
      ? true
      : transparent;

  if (currentTransparent === shouldBeTransparent) {
    return;
  }

  mat.transparent = shouldBeTransparent;

  if (mat.needsUpdate !== undefined) {
    mat.needsUpdate = true;
  }
}

export function getObjectOpacity(object: THREE.Object3D): number {
  let opacity = 1.0;
  let found = false;

  forEachMaterial(
    object,
    (mat: any) => {
      if (!found) {
        opacity = mat.opacity ?? 1.0;
        found = true;
      }
    },
    { skipCurrencySymbols: false }
  );

  return opacity;
}

export function resetGhostMaterialsToFullOpacity(object: THREE.Object3D): void {
  setObjectOpacity(object, 1.0, {
    preserveTransmission: true,
    skipCurrencySymbols: true,
  });
}

export function forEachMaterial(
  object: THREE.Object3D,
  callback: (
    material: THREE.Material,
    mesh: THREE.Mesh,
    childName: string
  ) => void,
  options?: {
    skipCurrencySymbols?: boolean;
    skipPacmanParts?: boolean;
    objectKey?: string;
  }
): void {
  const skipCurrencySymbols = options?.skipCurrencySymbols === true;
  const skipPacmanParts = options?.skipPacmanParts === true;
  const objectKey = options?.objectKey;

  object.traverse((child) => {
    if ((child as any).isMesh && (child as any).material) {
      const mesh = child as THREE.Mesh;
      const childName = child.name || "";

      if (skipCurrencySymbols && isCurrencySymbol(childName)) {
        return;
      }

      if (
        skipPacmanParts &&
        objectKey === "pacman" &&
        isPacmanPart(childName)
      ) {
        return;
      }

      if (Array.isArray(mesh.material)) {
        mesh.material.forEach((mat: any) => {
          callback(mat, mesh, childName);
        });
      } else {
        callback(mesh.material as any, mesh, childName);
      }
    }
  });
}
